import React, { Component } from "react";
import { connect } from "react-redux";
import { Field, reduxForm } from "redux-form";
import { createNumberMask } from "redux-form-input-masks";

const currencyMask = createNumberMask({
  prefix: "Rp. ",
  suffix: ",-",
  locale: "id-ID",
});
const renderField = ({
  input,
  label,
  type,
  limit,
  readOnly,
  meta: { touched, error, warning },
}) => (
  <div className="form-group">
    <label htmlFor="" className="text-black">
      {label}
    </label>
    <input
      {...input}
      type={type}
      className="form-control"
      min="0"
      step="0.001"
      maxLength={limit}
      readOnly={readOnly}
    />
    {touched &&
      ((error && <p className="invalid-feedback">{error}</p>) ||
        (warning && <p>{warning}</p>))}
  </div>
);
class FormEditPindahBarang extends Component {
  constructor(props) {
    super(props);
    this.state = {};
  }

  componentDidMount() {
    const date = new Date();
    this.props.change(
      "tanggal",
      `${date.getFullYear()}-${date.getMonth() + 1}-${date.getDate()}`
    );
  }
  render() {
    return (
      <form onSubmit={this.props.handleSubmit}>
        <div className="row">
          <div className="col-md-4 col-lg-4">
            <label htmlFor="">Kode Barang</label>
            <Field
              name="kodeBarang"
              component="select"
              value="CASH"
              className="form-control"
            >
              <option value="DEFAULT" key="DEFAULT">
                silahkan pilih
              </option>
            </Field>
          </div>
          <div className="col-md-4 col-lg-4">
            <Field
              name="kadar"
              label="Kadar"
              type="number"
              component={renderField}
              normalize={(value) => value && value.toUpperCase()}
            />
          </div>
          <div className="col-md-4 col-lg-4">
            <Field
              name="harga"
              label="Harga (%)"
              type="number"
              component={renderField}
              normalize={(value) => value && value.toUpperCase()}
            />
          </div>
          <div className="col-md-3 col-lg-3">
            <Field
              name="pkg"
              label="Pkg (Gr)"
              type="number"
              component={renderField}
              normalize={(value) => value && value.toUpperCase()}
            />
          </div>
          <div className="col-md-3 col-lg-3">
            <Field
              name="bruto"
              label="Bruto (Gr)"
              type="number"
              component={renderField}
              normalize={(value) => value && value.toUpperCase()}
            />
          </div>
          <div className="col-md-3 col-lg-3">
            <Field
              name="netto"
              label="Netto (Gr)"
              type="number"
              component={renderField}
              normalize={(value) => value && value.toUpperCase()}
            />
          </div>
          <div className="col-md-3 form-group">
            <label htmlFor="">Ongkos (Rp)</label>
            <Field
              name="rp"
              component="input"
              type="tel"
              className="form-control"
              {...currencyMask}
            />
          </div>
          <div className="col-lg-12 col-md-12">
            <div className="text-right">
              <button className="btn btn-primary" type="submit">
                Simpan
              </button>
            </div>
          </div>
        </div>
      </form>
    );
  }
}

FormEditPindahBarang = reduxForm({
  form: "FormEditPindahBarang",
  enableReinitialize: true,
})(FormEditPindahBarang);
export default connect()(FormEditPindahBarang);
